.App {
   width: 100vw;
   height: 100vh;
   background-color: #f7f7fc;
   overflow-x: hidden;
   color: #3b464e;
}
.App::-webkit-scrollbar {
   display: none;
}

/* ------------------------------------------------------------------------------------------------------------*/
/* ------------------------------------------------------------------------------------------------------------*/
/* HEADER */
header {
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 5px 15px;
   height: 50px;
   width: 100%;
   background: #f7f7fc;
   position: fixed;
   top: 0;
   left: 0;
   z-index: 100;
   box-shadow: 0 3px 20px rgba(0, 0, 0, 0.1);
}

.logo {
   min-width: 40px;
   width: 40px;
}

.shoppingCart {
   position: relative;
   width: 40px;
   height: 40px;
   display: flex;
   justify-content: center;
   align-items: center;
   cursor: pointer;
}
.shoppingCart .cart {
   font-size: 30px;
}
.shoppingCart .cart_content {
   width: 20px;
   height: 20px;
   background-color: #dc143c;
   border-radius: 100%;
   min-width: 20px;
   min-height: 20px;
   position: absolute;
   top: 0;
   right: 0;
   display: flex;
   align-items: center;
   justify-content: center;
}
.shoppingCart .cart_content p{
   font-size: 14px;
   font-weight: 500;
   color:  #f7f7fc;
}

/* ------------------------------------------------------------------------------------------------------------*/
/* ------------------------------------------------------------------------------------------------------------*/
/* MAIN */
main {
   width: 100%;
   height: auto;
   margin-top: 50px;
}
.mainContainer {
   width: 100%;
   height: auto;
}

/* ------------------------------------------------------------------------------------------------------------*/
/* ------------------------------------------------------------------------------------------------------------*/
/* MOTTO */
.motto {
   width: 100%;
   height: 200px;
   overflow: hidden;
   display: flex;
   align-items: center;
   justify-content: right;
   position: relative;
   background-image:  url("./Images/blueberry-pie.jpg");
   background-size: cover;
   background-position: center;
}
.mottoText {
   width: 200px;
   color: #f7f7fc;
   text-align: center;
   font-size: 2rem;
   line-height: 1.5;
   letter-spacing: 0.3rem;
   text-shadow: 0 1px 0 #ccc, 0 2px 0 #ccc, 0 20px 20px rgba(0,0,0,.2);
}

/* ------------------------------------------------------------------------------------------------------------*/
/* ------------------------------------------------------------------------------------------------------------*/
/* PRODUCTS */
.products {
   width: 100%;
   padding: 15px 10px;
}
/* ------------------------------------------------------------------------------------------------------------*/
/* subMenu */
.menuContainer {
   width: 100%;
}
.subMenu, .viewAll {
   display: flex;
   justify-content: space-between;
   align-items: center;
}
.subMenu h3 {
   font-size: 16px;
   font-weight: 600;
   letter-spacing: 0.2rem;
   text-transform: uppercase;
}
.subMenu .viewAll p {
   font-size: 14px;
   font-weight: 600;
   color: #dc143c;
}
.subMenu .viewAll i {
   background-color: #dc143c;
   width: 20px;
   height: 20px;
   min-width: 20px;
   border-radius: 5px;
   display: flex;
   align-items: center;
   justify-content: center;
   margin-left: 10px;
}
.subMenu .viewAll i .MuiSvgIcon-root {
   font-size: 20px;
   color: #f7f7fc;
}
/* ------------------------------------------------------------------------------------------------------------*/
/* rowContainer */
.rowContainer {
   display: flex;
   align-items: center;
   overflow-x: scroll;
   overflow-y: hidden;
   gap: 15px;
   margin: 10px 0;
   padding: 10px 0;
}
.rowContainer::-webkit-scrollbar {
   display: none;
}
.rowProductCard {
   width: 120px;
   min-width: 100px;
   height: 160px;
   border-radius: 5px;
   display: flex;
   align-items: center;
   justify-content: space-between;
   flex-direction: column;
   padding: 5px;
   box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
   transition: 0.5s;
   cursor: pointer;
}
.rowProductCard .rowImg {
   width: 100px;
   min-width: 80px;
   height: 100px;
   border-radius: 5px;
   overflow: hidden;
   transition: 0.5s;
}
.rowProductCard .rowImg img{
   width: 100%;
   height: 100%;
   object-fit: cover;
   object-position: center;
   border-radius: 5px;
   overflow: hidden;
}
.rowProductCard h3 {
   letter-spacing: 0.05rem;
   font-size: 18px;
   transition: 0.5s;
}
.rowProductCard .rowMenu {
   background-color: #dc143c;
   width: 20px;
   height: 20px;
   min-width: 20px;
   border-radius: 5px;
   display: flex;
   align-items: center;
   justify-content: center;
   transition: 0.5s;
}
.rowProductCard .rowMenu .MuiSvgIcon-root {
   font-size: 20px;
   color: #f7f7fc;
}

.rowProductCard:hover .rowImg img, 
.rowProductCard.active .rowImg img {
   width: 120%;
   height: 120%;
   transition: 0.5s;
}
.rowProductCard:hover h3, 
.rowProductCard.active h3 {
   color: #f7f7fc;
   
}
.rowProductCard:hover, 
.rowProductCard.active {
   background: #a1b6c7;
}
.rowProductCard:hover .rowMenu, 
.rowProductCard.active .rowMenu {
   background-color: #f7f7fc;
}
.rowProductCard:hover .rowMenu .MuiSvgIcon-root, 
.rowProductCard.active .rowMenu .MuiSvgIcon-root {
   color: #dc143c;
}
/* ------------------------------------------------------------------------------------------------------------*/
/* productItem */
.productItem {
   display: flex;
   justify-content: space-around;
   align-items: center;
   flex-wrap: wrap;
}
.itemCard {
   width: 150px;
   height: 220px;
   position: relative;
   border-radius: 5px;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   align-items: center;
   margin: 10px 10px;
   background-color: #fff;
   box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}
.itemCard .itemImg {
   width: 150px;
   height: 150px;
   border-radius: 5px;
   padding: 5px;
}
.itemCard .itemImg img {
   width: 100%;
   height: 100%;
   object-fit: cover;
   border-radius: 5px;
}
.itemContent {
   width: 100%;
   padding: 5px;
   text-align: center;
}
.itemContent h3 {
   font-size: 18px;
}
.itemContent .bottom {
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 5px ;
}
.itemContent .bottom .price {
   font-size: 16px;
   color: #dc143c;
}
.itemContent .bottom .price span {
   font-size: 15px;
}
.itemContent .bottom .addToCart {
   background-color: #dc143c;
   width: 25px;
   height: 25px;
   border-radius: 100%;
   display: flex;
   align-items: center;
   justify-content: center;
   box-shadow: 0 0 20px rgba(220, 20, 60, 0.2);
   cursor: pointer;
   transition: 0.5s;
}
.itemContent .bottom .addToCart .MuiSvgIcon-root {
   font-size: 16px;
   color: #f7f7fc;
}

/* ------------------------------------------------------------------------------------------------------------*/
/* ------------------------------------------------------------------------------------------------------------*/
/* SHOPPING CART*/
.cartCheckOut {
   width: 360px;
   height: calc(100% - 50px);
   background: #a1b6c7;
   position: fixed;
   right: 0;
   top: 50px;
   z-index: 105;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: space-between;
   padding: 10px;
   transform: translateX(200%);
   transition: transform 0.8s ease-in-out;
   box-shadow: 0 3px 20px rgba(0, 0, 0, 0.3);
   overflow: auto;
}
.cartCheckOut.active {
   transform: translateX(0%);
}
.cartTitle h3 {
   font-size: 16px;
   font-weight: 600;
   letter-spacing: 0.2rem;
   text-transform: uppercase;
   text-align: center;
}
/* ------------------------------------------------------------------------------------------------------------*/
/* cart items*/
.cartItems {
   width: 100%;
   min-height: 280px;
   max-height: 280px;
   background-color: #f7f7fc;
   margin: 10px 0;
   overflow: auto;
   border-radius: 5px;
}
.itemsInCart {
   display: flex;
   align-items: center;
   margin: 8px;
   width: 100%;
   max-width: 320px;
   padding-right: 5px;
   background: #fff;
   border-radius: 5px;
}
.itemsInCart .cartImg {
   width: 60px;
   min-width: 60px;
   height: 60px;
   min-height: 60px;
   border-radius: 10px;
   margin-right: 10px;
}
.itemsInCart .cartImg img {
   width: 100%;
   height: 100%;
   border-radius: 5px;
   object-fit: cover;
   object-position: center;
   overflow: hidden;
}
.itemsInCart .cartContent .cartName {
   font-size: 16px;
}
.itemsInCart .cartContent .cartQuantity {
   font-size: 16px;
   display: flex;
   align-items: center;
   justify-content: space-between;
   width: 150px;
}
.itemsInCart .cartContent .cartQuantity .quantity {
   color: #dc143c;
   width: 60px;
   display: flex;
   align-items: center;
   justify-content: space-between;
   cursor: pointer;
}
.itemsInCart .cartPrice {
   font-size: 18px;
   font-weight: 600;
   margin-left: auto;
   padding-top: 18px;
}
.itemsInCart .cartPrice .cartPriceSign {
   font-size: 17px;
}
/* ------------------------------------------------------------------------------------------------------------*/
/* cart imput*/
.imputInfo {
   margin: 10px 0;
}
.imputInfo {
   width: 100%;
}
.imputInfo form {
   display: flex;
   flex-direction: column;
   align-items:flex-start;
   gap: 5px;
}
.imputInfo form input {
   width: 100%;
}
/* ------------------------------------------------------------------------------------------------------------*/
/* cart submit*/
.submitTotal {
   width: 100%;
   display: flex;
   justify-content: space-between;
   align-items: center;

}
.submitTotal h3 {
   font-size: 18px;
}
.submitTotal h3 span {
   font-size: 17px;
   color: #dc143c;
}
.submitTotal .submitBtn {
   background-color: #dc143c;
   border: none;
   border-radius: 20px;
   color: #f7f7fc;
   padding: 8px 24px;
   text-decoration: none;
   margin: 4px 2px;
   cursor: pointer;
}
